@import url(https://fonts.googleapis.com/css?family=Montserrat:700);
:root {
  --black: #111;
  --theme: #678;
  --background: #F1F3F5;
  --blue: #00BFED;
  --light-green: #00D7C5;
  --purple: #AD1DE6;
  --bg-black: #303E4B;
  --yellow: #E99523;
  --red: #D81C60;
  --font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: -moz-none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  color: #111;
  color: var(--black);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #678;
  background-color: var(--theme);
  outline: 1px solid #111;
  outline: 1px solid var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    width: 100%;
    height: 20vh;
    z-index: 10;
    top: 0;
    background: transparent;
}

#header-container {
    width: 87%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.ellipse {
    position: absolute;
    height: 25vh;
    z-index: -1;
}

#header-right ul {
    list-style: none;
}


#header-right li {
    display: inline-block;
    margin-left: 1vw;
    color: var(--black);
}

#header-right li:last-child {
    background: -webkit-gradient(linear,left top, left bottom,from(#8eb7f5),to(#40e1dc));
    background: -webkit-linear-gradient(#8eb7f5,#40e1dc);
    background: linear-gradient(#8eb7f5,#40e1dc);
    border-radius: 5vw;
    color: white;
    box-shadow: 2px 8px 25px rgba(0, 0, 0, 0.16);
    font-weight: 600;
}

#header-right a:last-child {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 1vw 2vw;
}

main {
    width: 100%;
}

footer {
    width: 100%;
    bottom: 0;
    height: 25vh;
    position: relative;
    background: url(/a2z-frontend/static/media/footer.7f435832.svg);
    background-repeat: no-repeat;
    background-size: cover;
}


#footer-container {
    width: 75%;
    margin: 0 auto;
    height: 100%;
    box-sizing: border-box !important;
    padding-top: 7vh;
    position: relative;
}

#footer-links-container {
    position: absolute;
    right: 0;
    margin-top: -20px;
}

.footer-link {
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 8px 18px rgba(0, 0, 0, 0.16);
    margin: 7px;
    height: 2.3vw;
    width: 7vw;
    background: var(--black);
    border-radius: 7px;
    text-decoration: none;
    color: white;
    font-size: 1.5em;
    font-weight: bold;
    letter-spacing: 1px;
}

.footer-link img {
    width: 60%;
    margin-right: 5px;
}


#claim {
    text-align: center;
    opacity: 0.7;
    position: absolute;
    bottom: 2vh;
    left: 40%;
}

#claim p {
    font-size: 1.2em;
}

.side-links-container {
    position: fixed;
    top: 45%;
    left: 0px;
}


.side-link {
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
    margin-top: 25px;
    background: var(--black);
    box-shadow: 2px 8px 30px rgba(0, 0, 0, 0.16);
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

.side-link:hover {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-link a {
    display: block;
    padding: 25px 30px 25px 70px;
}

.side-link img {
    width: 32px;
}

@media only screen and (min-width: 2000px) {

    #header-right li {
        font-size: 2rem;
    }

    .footer-link img {
        width: 70%;
        margin-right: 20px;
    }

    .side-links-container {
        -webkit-transform: scale(0.4) translateX(-90px);
                transform: scale(0.4) translateX(-90px);
        top: 25%;
    }
}

@media only screen and (min-width: 1024px) and (max-width: 1940px) {

    #footer-links-container {
        margin-top: -10px;
    }

    .footer-link {
        border-radius: 4px;
        font-size: 0.8em;
        letter-spacing: 0px;
        font-size: 0.6em;
    }

    .footer-link img {
        width: 50%;
        margin-right: 0;
    }

    #claim p {
        font-size: 0.7em;
    }

    .side-links-container {
        -webkit-transform: scale(0.4) translateX(-90px);
                transform: scale(0.4) translateX(-90px);
        top: 25%;
    }

    .side-link:hover {
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
    }
}

@media only screen and (max-width: 1023px) {

    header {
        height: 12vh;
    }

    #header-container {
        width: 90%;
    }

    .footer-link {
        border-radius: 4px;
        font-size: 1vh;
        height: 2.5vh;
        width: 8vh;
        letter-spacing: 0px;
    }

    footer {
        height: 125px;
    }
    
    #footer-container {
        padding-top: 4%;
    }

    #claim {
        left: 35%;
    }

    #claim p {
        font-size: 0.5em;
    }

    .side-links-container {
        -webkit-transform: scale(0.3) translateX(-160px);
                transform: scale(0.3) translateX(-160px);
        top: 25%;
    }

    .side-link:hover {
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
    }

}

@media only screen and (max-width: 480px) {

    header {
        height: 10vh;
    }

    #header-container {
        width: 85%;
    }

    .ellipse {
        display: none;
    }

    nav {
        display: none;
    }

    footer {
        background: #eee;
        min-height: 20vh;
    }

    .footer-background {
        display: none;
    }

    #footer-container {
        padding-top: 3vh;
    }

    .footer-link {
        margin-top: 0px;
        border-radius: 2px;
        font-size: 0.5em;
        height: 3vh;
        width: 9vh;
        text-align: center;
        letter-spacing: 0px;
        padding-right: 5px;
    }

    .footer-link img {
        width: 30%;
    }

    #claim {
        left: 0%;
        text-align: left;
    }

    #claim p {
        font-size: 0.6em;
    }

}

@media screen and (orientation: landscape) and (max-width: 812px) {
    
    header {
        height: 17vh;
    }

    nav {
        display: none;
    }

    #footer-links-container {
        bottom: 0px;
    }

    .footer-link {
        margin-top: 7px;
        border-radius: 3px;
        font-size: 1vw;
        height: 2vw;
        width: 7.5vw;
        letter-spacing: 0px;
    }

    .footer-link img {
        width: 40%;
        margin-right: 0;
    }

    #claim p {
        font-size: 0.5em;
    }

    .side-links-container {
        -webkit-transform: scale(0.3) translateX(-160px);
                transform: scale(0.3) translateX(-160px);
        top: 15%;
    }

    .side-link:hover {
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
    }
}
@media only screen and (min-width: 1024px) {
    .logo {
        font-size: 2vw;
        text-shadow: 6px 6px 11px rgba(0, 0, 0, 0.16);
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
    }
}

@media only screen and (max-width: 480px) {
    .logo {
        font-size: 3.5vh;
    }
}
#hamburger-menu {
    position: relative;
    min-width: 27px;
    min-height: 15px;
}

.bar {
    width: 100%;
    height: 3px;
    background-color: var(--black);
    position: absolute;
}

.bar:first-child {
    top: 0;
}

.bar:nth-child(1) {
    top: 50%;
    margin-top: -1.5px;
}

.bar:last-child {
    bottom: 0;
}

@media only screen and (min-width: 768px) {
    #hamburger-menu {
        display: none;
    }
}

@media only screen and (max-width: 812px) and (orientation: landscape) {
    #hamburger-menu {
        display: block;
    }
}
.backdrop-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 12;
    background: rgba(0, 0, 0, 0.25);
}
.sliding-nav-container {
    height: 100%;
    width: 70%;
    position: absolute;
    z-index: 13;
    background-color: var(--bg-black);
    box-shadow: 8px 10px 20px 0 rgba(46,61,73,.15);
    top: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

#sliding-nav-header {
    width: 80%;
    margin: 5vh auto;
    display: flex;
    justify-content: space-between;
}

#back-button {
    width: 0; 
    height: 0; 
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent; 
    border-right:12px solid #fff;
}

@media only screen and (min-width: 1024px) {
    .sliding-nav-container {
        display: none;
    }
}

@media screen and (orientation: landscape) and (max-width: 1024px)  {
    .sliding-nav-container {
        width: 30%;
    }
}
.sliding-nav-link-container {
    line-height: 10vh;
    padding-left: 8vw;
    color: #fff;
    border-bottom: 0.5px solid #678;
}

@media screen and (orientation: landscape) and (max-width: 768px) {
    .sliding-nav-link-container {
        padding-left: 2vw;
    }
}
#home-page-container {
    padding: 5vh 0 25vh 0;
    text-align: center;
}

.heading {
    margin-bottom: 5px;
    color: var(--black);
    font-size: 2em;
}

.sub-heading {
    color: var(--theme);
    font-size: 1em;
    max-width: 75vw;
    margin: 17px auto;
}

@media only screen and (min-width: 2000px) {

    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 3vw;
    }

    .sub-heading {
        font-size: 1.5vw;
    }
}

@media only screen and (max-width: 768px) {

    #home-page-container {
        padding: 10vh 0; 
    }

    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 4vw;
    }

    .sub-heading {
        font-size: 2vw;
    }
}

@media only screen and (max-width: 480px) {
    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 6vw;
    }

    .sub-heading {
        font-size: 4vw;
    }
}
.cards-container {
    width: 75%;
    margin: 7vh auto;
    display: grid;
    grid-template-columns: 100%;
}

@media only screen and (max-width: 1023px) and (min-width: 481px) {
    .cards-container {
        grid-template-columns: 33% 33% 33%;
        margin: 4vh auto;
    }
}

@media only screen and (min-width: 1024px) {
    .cards-container {
        width: 90%;
        grid-template-columns: 25% 25% 25% 25%;
    }
}
.card-link {
    height: 18vw;
    line-height: 18vw;
    text-align: left;
    margin-bottom: 7vw;
    box-shadow: 2px 8px 30px rgba(0, 0, 0, 0.16);
    border-radius: 4px;
}

.card-link a {
    text-decoration: none;
    display: block;
    color: white;
    font-size: 0.9em;
    font-weight: 500;
    width: 100%;
    height: 100%;
}


.card-link img {
    width: 10%;
    margin-top: -4px;
    margin-right: 5%;
    margin-left: 5%;
    vertical-align: middle;
}

@media only screen and (max-width: 400px) {
    .card-link a {
        font-size: 0.7em;
    }
}
 

@media only screen and (min-width: 481px) {
    .card-link {
        line-height: 2.5vw;
        text-align: center;
        margin: 0 2vw;
        margin-top: 0;
        margin-bottom: 3.5vw;
        border-radius: 7px;
    }

    .card-link a {
        box-sizing: border-box;
        padding: 25% 10%;
        font-size: 0.7em;
    }

    .card-link span {
        display: block;
    }

    .card-link img {
        width: 30%;
        margin-bottom: 10px;
    }
}

@media only screen and (min-width: 769px) {
    .card-link {
        height: 16vw;
        line-height: 1.6vw;
        margin-top: 0;
        margin-bottom: 3vw;
        border-radius: 15px;
    }

    .card-link a {
        padding: 27% 10%;
        font-size: .9em;
    }
}

@media only screen and (min-width: 1400px) {
    .card-link a {
        font-size: 1.2vw;
    }
}

#landing-page-container {
    width: 100%;
    top: 0;
    left: 0;
}

.background-svg-1 {
    height: 60vh;
    position: fixed;
    top: 20%;
    right: 7%;
}

.background-svg-2 {
    height: 75vh;
    position: fixed;
    z-index: -1;
    bottom: 0;
    right: 0;
}

#special-container {
    margin-top: 17vh;
    margin-left: 10vw;
}

#special-container h1 {
    font-size: 2.5vw;
}

#special-container p {
    font-size: 1.5vw;
    margin-top: 2vh;
    font-weight: 500;
    color: var(--theme);
}

#special-container div {
    margin-top: 4vh;
    font-size: 1.4vw;
    font-weight: bold;
    z-index: 10;
    max-width: 13vw;
}

#special-container div a {
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    background: -webkit-gradient(linear,left top, left bottom,from(#fca15a),to(#f587b0));
    background: -webkit-linear-gradient(#fca15a,#f587b0);
    background: linear-gradient(#fca15a,#f587b0);
    height: 4vw;
    border-radius: 10vw;
    box-shadow: 2px 8px 22px rgba(0, 0, 0, 0.16);
}


@media only screen and (max-width: 768px) {
    .background-svg-1{
        width: 70vw;
        height: auto;
        top: 50vh;
        left: 50%;
        margin-left: -35vw;
    }

    .background-svg-2 {
        width: 100vw;
        height: auto;
    }

    #landing-page-container {
        text-align: center;
    }

    #special-container {
        margin-top: 14vh;
        margin-left: 0;
    }
    
    #special-container h1 {
        font-size: 3.5vh;
    }

    #special-container p {
        font-size: 2vh;
    }

    #special-container div {
        font-size: 1.7vh;
        margin: 4vh auto;
        max-width: 15vh;
    }

    #special-container div a {
        height: 5vh;
    }
}

@media screen and (orientation: landscape) and (max-width: 768px) {
    .background-svg-1 {
        display: none;
    }

    .background-svg-2 {
        width: 100vw;
        height: auto;
    }

    #special-container h1 {
        font-size: 2.5vw;
    }

    #special-container p {
        font-size: 1.5vw;
        color: black;
    }

    #special-container div {
        font-size: 1.4vw;
        margin: 4vh auto;
        max-width: 12vw;
    }

    #special-container div a {
        height: 4vw;
    }
}
